@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.container-center{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #121212;
}

.login{
    background-color: #eaeaec;
    width: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.logo-area{
    display: flex;
    justify-content: center;
    background-color: #181c2e;
    width: 100%;
}

.logo-area img{
    padding: 20px;
    width: 170px;
    height: 130px;
}

form{
    margin-top: 1.5em;
    width: 90%;
    display: flex;
    flex-direction: column;
}

form h1{
    text-align: center;
    margin-bottom: 0.5em;
    color: #181c2e;
}

.login input{
    margin-bottom: 15px;
    height: 35px;
    border: 0;
    border-radius: 7px;
    padding: 10px;
    font-size: 15px;
    background-color: #FFF;
}

form button{
    height: 35px;
    border: 0;
    border-radius: 7px;
    background-color: #181c2e;
    color: #FFF;
    font-size: 1.3em;
}

.login a{
    margin: 1.5em 0;
    color: black;
    cursor: pointer;
}

.conta{
    background-color: #eaeaec;
    width: 180px;
    margin-top: 50px;
    padding: 5px;
    padding-left: 10px;
    border-radius: 8px;
    opacity: 0.6;
}

.conta span{
    font-size: 1.2em;
    display: inline-flex;
    margin-bottom: 8px;
}

.conta p{
    margin-top: 1px;
    margin-bottom: 1px;
}
.dashboard{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dashboard span{
    margin:  2em 0;
    font-weight: 600;
    font-size: 1.2em;
}

.new{
    float: right;
    margin-bottom: 1.5em;
    background-color: #83bf02;
    color: #FFF;
    border: 0;
    padding: .5em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 1.1em;
    border-radius: 6px;
    transition: ease-in 0.2s;
}

.new svg{
    margin-right: 5px;
}

.new:hover{
    background-color: #5fd204;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

table{
    border: 1px solid #CCC;
    border-collapse: collapse;
    margin: 0;
    padding: 0;
    width: 100%;
    table-layout: fixed;
}

table caption{
    font-size: 1.5em;
    margin: .5em 0 .75em;
}

table tr{
    background-color: #f8f8f8;
    border: 1px solid #ddd;
    padding: .35em;
}

table th,
table td{
    padding: .62em;
    text-align: center;
}

table th{
    font-size: .85em;
    letter-spacing: .1em;
    text-transform: uppercase;
}

table td .action{
    border: 0;
    padding: 5;
    margin-right: 5px;
    align-items: center;
    display: inline-block;
    border-radius: 4px;
    background-color: #3583f6;
}

table td .action svg{
    vertical-align: middle;
}

table td .action-a{
    background-color: #f6a935;
    border-radius: 4px;
    align-items: center;
    display: inline-table;
    vertical-align: middle;
}

table td .action-a svg{
    vertical-align: middle;
    margin-right: 5px;
    margin-left: 5px;
    margin-bottom: 1px;
    margin-top: 1px;
}

table td .badge{
    padding: 3px;
    border-radius: 3px;
    color: #FFF
}

.btn-more{
    margin: 1.5em 0;
    padding: .5em 1em;
    height: 35px;
    border: 0;
    border-radius: 5px;
    background-color: #181c2e;
    color: #FFF;
    font-size: 1.1em;
}

.carregando-chamados{
    text-align: center;
    margin-top: 15;
}

@media screen and (max-width: 600px) {
    table{
        border: 0;
    }
    
    table caption{
        font-size: 1.3em;
    }

    table thead{
        border: none;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }

    table tr{
        border-bottom: 3px solid #ddd;
        display: block;
        margin-bottom: .65em;
    }

    table td{
        border-bottom: 1px solid #ddd;
        display: block;
        font-size: .8em;
        text-align: right;
    }

    table td::before{
        content: attr(data-label);
        float: left;
        font-weight: bold;
        text-transform: uppercase;
    }

    table td:last-child{
        border-bottom: 0;
    }

    table td .action{
        border-radius: 4px;
        position: relative;

    }

    table td .action-a{
        border-radius: 4px;
        position: relative;
        bottom: 0.7px;
    }

    table td .action-a svg{
        margin-bottom: 1.3px;
     }
}
.sidebar{
    margin: 0;
    padding: 0;
    width: 200px;
    background-color: #181c3e;
    position: fixed;
    height: 100%;
    overflow: auto;
}

.sidebar div{
    background: url(/reacthelpdesk/static/media/cover.6a293bc8.png);
    background-color: #181c3e;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 150px;
}

.sidebar div{
    padding-top: 30px;
}

.sidebar div img{
    border-radius: 50%;
    display: block;
    margin: auto;
    width: 90px;
    height: 90px;
    -webkit-filter: drop-shadow(2px 3px 6px #121212);
    filter: drop-shadow(2px 3px 6px #121212);
    object-fit: cover;
}

.sidebar a{
    display: block;
    color: rgba(255, 255, 255, 0.7);
    padding: 16px;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: ease-in-out .4s;
}

.sidebar a svg{
    margin-right: .5em;
    color: #FFF;
}

.sidebar a:hover{
    background-color: #121212;
    color: #FFF;
}

.content{
    margin-left: 200px;
    padding: 1px 16px;
}

.logout{
    margin-left: -4px;
}

@media screen and (max-width: 700px){
    .sidebar{
        width: 100%;
        height: auto;
        position: relative;
    }

    div.content{
        margin-left: 0;
    }

    .sidebar a{
        float: left;
    }

    .sidebar div{
        display: none;
    }

    .sidebar a svg{
        display: none;
    }
}

@media screen and (max-width: 400px){
    .sidebar{
        text-align: center;
        float: none;
    }

    .sidebar a svg{
        display: none;
    }
}


.title{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 1em 0;
    border-radius: 5px;
    background-color: #f8f8f8;
    padding: .8em;
}

.title span{
    margin-left: 1em;
    font-size: 1.5em;
}
.modal{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0,0,0, 0.6);
    z-index: 99;
}

.modal .container{
    position: fixed;
    max-width: 600px;
    top: 15%;
    left: 0;
    right: 0;
    margin: 0 auto;
    padding: 4em 2rem;
    background-color: #FFF;
    box-shadow: 0 0 20px rgba(0,0,0, 0.8);
}

.close{
    background-color: #f65835;
    border: 0;
    color: #FFF;
    position: absolute;
    top: 15px;
    left: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 15px;
    border-radius: 5px;
}

.close svg{
    margin-right: 5px;
}

.modal h2{
    margin-bottom: 1.2em;
    font-size: 2em;
}

.modal span{
    font-weight: bold;
    font-size: 1.2em;
    color: #121212;
}

.modal span i{
    font-weight: 400;
    margin-right: 1em;
    padding: 2px 8px;
    border-radius: 3px;
}

.row{
    margin-bottom: 1em;
}

.modal p{
    padding-top: 0.5em;
    white-space: pre-wrap;
}
.container{
    display: flex;
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 8em;
    align-items: center;
}

.label-avatar{
    width: 280px;
    height: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
}

.label-avatar input{
    display: none;
}

.label-avatar span{
    z-index: 99;
    position: absolute;
    opacity: 0.6;
    transition: all .5s;
}

.label-avatar span:hover{
    opacity: 1;
    -webkit-transform: scale(1.7);
            transform: scale(1.7);
}

.form-profile img{
    margin-bottom: 1em;
    border-radius: 50%;
    object-fit: cover;
}

.form-profile label{
    margin-bottom: 1em;
    font-size: 1.4em;
}

.form-profile input, textarea, select{
    margin-bottom: 1em;
    padding: .7em;
    border: 0;
    border-radius: 5px;
    max-width: 600px;
}

.form-profile input:disabled{
    cursor: not-allowed;
}

.form-profile button{
    max-width: 600px;
}

form textarea{
    height: 105px;
    resize: none;
}

form input[type="radio"]{
    margin: 15px 0;
}

form input[type="radio"]:not(:first-child){
    margin-left: 15px;
}

.status span{
    padding-left: .5em;
    font-size: 1.3em;
}
*{
    margin: 0;
    padding: o;
    box-sizing: border-box;
}

*:focus{
    outline: 0;
}

html, body, #root{
    height: 100%;
}

body{
    font: 14px 'Roboto', sans-serif;
    background-color: #EFEFEF;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}

button{
    cursor: pointer;
}
